body {
  max-width: 96vw;
  width: 1600px;
  margin: 0 auto;
}

#chart-container {
  background-color: #0000;
  margin: 0;
  padding: 2% 0;
}

#bar-chart {
  max-height: 80vh;
  height: 800px;
  position: relative;
}

#title {
  text-align: center;
  font-size: 2em;
}

#subhead {
  text-align: center;
  font-size: 1.22em;
}

#tooltip {
  color: #fff;
  background-color: #333;
  border-radius: 5px;
  padding: 5px;
  font-family: sans-serif;
  font-size: 14px;
  position: absolute;
}

.hidden {
  display: none;
}

rect {
  stroke-width: 1px;
  stroke: #000;
  fill: #333;
  fill-opacity: .2;
}

.tick text {
  font-family: monospace;
  font-size: 20px;
}

